import { stdAggregate, stdAggregateFromCuboid } from "../statistics";
const DEFAULT_OPS = ['max', 'min', 'sum', 'mean', 'count'];
export class Cuboid {
    constructor(props) {
        const { dimensions, measures, ops = DEFAULT_OPS } = props;
        this.dimensions = dimensions;
        this.measures = measures;
        this.ops = ops;
        this.state = [];
    }
    setData(dataSource) {
        this.state = stdAggregate({
            dimensions: this.dimensions,
            measures: this.measures,
            ops: this.ops,
            dataSource
        });
        return this.state;
    }
    computeFromCuboid(cuboid) {
        const { ops, measures, dimensions } = this;
        this.state = stdAggregateFromCuboid({
            dimensions,
            measures,
            ops,
            dataSource: cuboid.getRawState()
        });
        return this.state;
    }
    get size() {
        return this.state.length;
    }
    getState(measures, operatorOfMeasures) {
        let data = [];
        const { state, dimensions } = this;
        for (let row of state) {
            let newRow = {};
            for (let dim of dimensions) {
                newRow[dim] = row[dim];
            }
            for (let i = 0; i < measures.length; i++) {
                const mea = measures[i];
                const op = operatorOfMeasures[i];
                newRow[mea] = row[mea][op];
            }
            data.push(newRow);
        }
        return data;
    }
    getRawState() {
        return this.state;
    }
}
